* {
  padding: 0;
  margin: 0;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-image: url('./Imagens/imagefundo.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

@media only screen and (max-width: 600px) {
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100vw;
  }
}